import React, { PureComponent } from 'react'
import './Sidebar.css'

class Sidebar extends PureComponent {
  static defaultProps = {}

  render() {
    return (
      <div className="Layout-Sidebar">
        Sidebar
      </div>
    )
  }
}

export default Sidebar



